.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  /* height: 100%; */
  display: flex;
  /* flex-direction: column; */
  justify-content: center;
  align-items: center;

  /* border: dashed darkorange 2px; */
}
.nav-links li {
  /* space around EACH nav-link */
  margin: 1rem;
  /* border: dotted 2px yellow; */
  padding: 0;
}

.nav-links a {
  border: 1px solid transparent;
  color: #292929;
  text-decoration: none;
  padding: 0.5rem;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  background: var(--bg-secondary);
  border-color: #292929;
  color: #292929;
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #292929;
  color: #292929;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: #292929;
  color: white;
}
